<template>
    <div class="changer">
        <div class="flags" :class="{'expanded': expanded}">
            <b-img @click="setLanguage('fr')" :class="{'inactive': !isFr()}" src="https://image.flaticon.com/icons/svg/321/321230.svg"></b-img>
            <b-img @click="setLanguage('en')" :class="{'inactive': !isEn()}" src="https://image.flaticon.com/icons/svg/321/321269.svg"></b-img>
        </div>
        <hr>
        <div @click="expanded = !expanded">
            <icon class="icon" :class="{'expanded': expanded}" icon="sort-up"></icon>
        </div>
    </div>
</template>

<script>
    import {setLanguage, getLanguage} from '../helpers/utils';

    export default {
        name: "LanguageChanger",
        data() {
            return {
                expanded: false
            }
        },
        methods: {
            setLanguage(lang) {
                setLanguage(lang);
                this.$router.go();
            },
            isFr() {
                return getLanguage() === 'fr';
            },
            isEn() {
                return getLanguage() === 'en';
            }
        }
    }
</script>

<style lang="scss" scoped>
    hr {
        margin: 0;
    }

    .changer {
        color: rgba(0, 0, 0, 0.7);
        width: 50px;
        background: linear-gradient(to right, rgb(255, 255, 255) 0%, rgb(237, 237, 237) 100%);
        border-bottom-left-radius: 30px;
        border-bottom-right-radius: 30px;
        text-align: center;
        cursor: pointer;

        .flags {
            padding: 5px 7px;
            margin-top: -82px;
            transition: margin-top 300ms ease;

            &.expanded {
                margin-top: 0;
            }

            img {
                width: 100%;
                transition: padding 300ms ease;

                &:active {
                    padding: 10px;
                }
            }
        }

        .icon {
            font-size: 30px;
            transition: 400ms ease;

            &:not(.expanded) {
                transform: rotate(180deg);
                margin-top: -10px;
            }

            &.expanded {
                margin-bottom: -10px;
            }
        }

        .inactive {
            filter: grayscale(0.3) opacity(0.5);
        }
    }
</style>
