import Vue from 'vue';
import App from './App.vue';
import router from "./router";
import './helpers/mixin';
import './helpers/directives';
import '../public/scss/global.scss';

import { BootstrapVue, IconsPlugin } from 'bootstrap-vue';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import SmartMenu from "./components/SmartMenu";
import Skill from "./components/Skill";
import ProjectPreview from "./components/ProjectPreview";
import IconLink from "./components/IconLink";
import LanguageChanger from "./components/LanguageChanger";

Vue.config.productionTip = false;

library.add(fas);
library.add(fab);

Vue.use(BootstrapVue);
Vue.use(IconsPlugin);
Vue.use(FontAwesomeIcon);
Vue.component('icon', FontAwesomeIcon);
Vue.component('smart-menu', SmartMenu);
Vue.component('skill', Skill);
Vue.component('project-preview', ProjectPreview);
Vue.component('icon-link', IconLink);
Vue.component('language-changer', LanguageChanger);

new Vue({
  router,
  render: h => h(App)
}).$mount('#app');
