<template>
    <icon :icon="icon" class="icon-link shadow-sm" :class="isMobileDevice() ? 'mobile' : 'pc'" @click="openLink"></icon>
</template>

<script>
    import { isMobileDevice } from '../helpers/utils'

    export default {
        name: "IconLink",
        props: {
            icon: {
                required: true
            },
            link: {
                type: String,
                required: true
            }
        },
        methods: {
            openLink() {
                window.open(this.link);
            },
            isMobileDevice() {
                return isMobileDevice();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .icon-link {
        cursor: pointer;
        display: inline;
        color: rgba(255, 255, 255, 0.9);
        padding: 8px;
        width: 3rem;
        height: 3rem;
        background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.2));
        border-radius: 15px;
        transition: 150ms ease;
    }

    .mobile {
        &:active {
            transform: scale(0.7);
        }
    }

    .pc {
        &:hover {
            transform: scale(1.2);
        }
    }
</style>
