import {isString} from "bootstrap-vue/esm/utils/inspect";

export function isMobileDevice() {
    return innerWidth < 600;
}

export function setLanguage(lang) {
    if (!lang)
        return;
    localStorage.setItem('lang', lang);
}

export function getLanguage() {
    let lang = localStorage.getItem('lang');
    if (!lang) {
        if (navigator.language.indexOf('fr') !== -1)
            lang = 'fr';
        else
            lang = 'en';
        localStorage.setItem('lang', lang);
    }
    return lang;
}

export function translate(values) {
    if (!values)
        return '';

    let trs = values;

    if (isString(values))
        trs = values.split('|');

    if (getLanguage() === 'en')
        return trs[0];
    else if (getLanguage() === 'fr')
        return trs[1];
    else
        return trs;
}
