<template>
    <div @click="openLink" class="image-box">
        <b-img class="image" :src="require('../assets/projects/' + image + '.png')"></b-img>
        <div class="overlay" v-if="!isMobileDevice()">
            <h3 class="text-center mt-3" style="letter-spacing: 1px">{{ title }}</h3>
            <hr>
            <p class="text-justify" style="font-size: 14pt">{{ description }}</p>
        </div>
        <div class="icon">
            <icon icon="globe-americas"></icon>
        </div>
    </div>
</template>

<script>
    import { isMobileDevice } from '../helpers/utils'

    export default {
        name: "project-preview",
        props: {
            title: {
                type: String,
                required: true
            },
            description: {
                type: String,
                default: ''
            },
            image: {
                type: String,
                required: true
            },
            link: {
                type: String,
                required: true
            }
        },
        methods: {
            openLink() {
                window.open(this.link, '_blank');
            },
            isMobileDevice() {
                return isMobileDevice();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .image-box {
        position: relative;
        width: 100%;
        max-width: 100%;
        height: 100%;
        max-height: 100%;
        overflow: hidden;
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
        cursor: pointer;

        .image {
            position: relative;
            width: 120%;
            vertical-align: middle;
            transition: 500ms ease;
            padding-right: 10%;
        }

        .overlay {
            position: absolute;
            background: rgba(99, 20, 107, 0.8);
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            padding: 0 2rem;
            transition: opacity 300ms ease;

            hr {
                border-top: 1px solid rgba(255, 255, 255, 0.8);
            }
        }

        .icon {
            position: absolute;
            top: 0;
            right: 0;
            background: rgba(0, 0, 0, 0.8);
            font-size: 20px;
            width: 40px;
            line-height: 40px;
            text-align: center;
            color: rgba(255, 255, 255, 0.8);
        }

        &:hover {
            .image {
                transform: scale(1.1);
            }

            .overlay {
                opacity: 1;
            }
        }
    }
</style>
